body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  height: 100% !important;
  width: 100% !important;
  background-color: #1c1c1c !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100%;
  padding: 0px 15px;
  box-sizing: border-box;
  max-width: 500px;
  margin: 0px auto;
}

.btn {
  font-family: 'Staatliches', cursive;
}

.section-title {
  font-family: 'Staatliches', cursive;
  margin-top: -18px;
  margin-bottom: 5px;
  font-size: 1.5em;
  text-align: center;
}

.section-footer {
  text-align: center;
  display: block;
  font-size: 0.5em;
  margin-top: 15px;
  margin-bottom: -15px;
}

.section-description {
  font-size: 0.5em;
  margin-bottom: -8px;
  margin-top: 5px;
}

.delay-1 {
  -webkit-animation-delay: 100ms;
          animation-delay: 100ms;
}

.delay-2 {
  -webkit-animation-delay: 300ms;
          animation-delay: 300ms;
}

.delay-3 {
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
}

.delay-4 {
  -webkit-animation-delay: 700ms;
          animation-delay: 700ms;
}

.delay-5 {
  -webkit-animation-delay: 900ms;
          animation-delay: 900ms;
}

.delay-5 {
  -webkit-animation-delay: 1100ms;
          animation-delay: 1100ms;
}

.form-group>button,
.full-width {
  width: 100%;
}

td>button.option-btn {
  width: 100%;
  font-size: .80em;
  margin-top: -2px;
  padding: 2.5px 15px;
}

.full-width-placement {
  height: 150px;
  background-size: cover;
  background-position: center center;
  background-color: #CCC;
}
.signin-wrapper {
  background-color: #F8F9FA;
  margin: 15px 0px;
  padding: 25px 0px;
}

.create-account-btn,
.signin-btn {
  width: 100%;
}

.signin-btn {
  margin-top: 10px;
}

.signin-header {
  font-family: 'Staatliches', cursive;
  text-align: center;
  font-size: 3em;
  margin-bottom: -10px;
}

.signin-subheader {
  text-align: center;
  font-size: 0.75em;
}

.gender-selection {
  text-align: center;
}

.gender-selection > label {
  margin: 0px 15px 15px 15px;
}

.gender-selection > label > input {
  margin-right: 15px;
}
.stat-container {
  text-align: center;
}

.stat-container > label {
  font-size: 0.5em;
  font-weight: bold;
}

.stat-container > button {
  width: 100%;
}

.stat-container > label {
  margin: 0px;
}

.stat-container > h3.green {
  color: #39af2b;
}

.stat-container > h3.red {
  color: #af2b2b;
}

.stat-spacer {
  padding: 7px;
}

h5.modal-title {
  font-family: 'Staatliches', cursive;
}

div.modal-body {
  font-size: 0.75em;
}

.day-picker {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  text-align: center;
  grid-column-gap: 10px;
  margin-bottom: 25px;
  margin-top: 25px;
}

.day-picker > div.selected {
  background-color: #353A3F;
}

.day-picker > div {
  background-color: rgb(118, 124, 131);
  color: #FFF;
  padding: 10px 0px;
  font-weight: bold;
  border-radius: 3px;
  font-size: 1em;
  cursor: pointer;
}
.todo-list-item > button {
  float: right;
  margin-top: -7px;
  width: 90px;
  text-align: center;
}

div.todo-list-item-wrapper:last-child {
  margin-bottom: -20px;
}

div.fitness-profile-photo {
  height: 50px;
  width: 50px;
  background-color: rgb(112, 112, 112);
  border-radius: 100%;
  border: solid medium #E2E3E5;
  background-size: cover;
  background-position: center top;
  margin: 0px auto;
}

.coach-image-wrapper {
  font-size: 0.5em;
}

.coach-image-wrapper > h4 {
  font-size: 1.5em;
  text-align: center;
  font-weight: bold;
  margin: 0px;
}

.coach-image-wrapper > span {
  display: block;
  text-align: center;
}

div.view-all-trainers {
  display: none;
}

div.view-all-trainers > span {
  margin-top: 10px;
  font-weight: bold;
  color: rgb(50, 85, 155);
  font-size: 0.75em;
  margin-bottom: -15px;
  text-align: center;
  display: block;
}

div.coach-image-wrapper {
  white-space: nowrap;
}
.container {
  background-color: #F8F9FA;
  margin: 15px 0px;
  padding: 25px 10px;
}
.navbar-brand {
  font-family: 'Staatliches', cursive;
}

.navbar {
  margin-top: 15px;
}

.navbar-nav {
  margin-left: auto;
}

.navbar ul {
  font-size: 0.75em;
  align-items: center;
}

.navbar ul li {
  cursor: pointer;
}
.container {
  background-color: #F8F9FA;
  margin: 15px 0px;
  padding: 25px 0px;
}

.footer {
  color: #FFF;
  font-size: 0.75em;
  text-align: center;
  margin-bottom: 25px;
  margin-top: -20px;
}
.App {
}

